.workout {
  width: 100%;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include vp--1023 {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__wrapper--center {
    justify-content: center;

    p {
      text-align: center;
    }
  }
}
