.input {
  input {
    height: 40px;
    max-width: 300px;
    width: 100%;
    margin-bottom: 60px;

    color: $color-grey;
    border: none;
    border-bottom: 1px solid $color-grey;
    outline: none;
    background-color: transparent;

    &::placeholder {
      color: $color-grey;

      @include vp--500 {
        font-size: 85%;
      }
    }
  }

  &--name {
    input {
      margin-bottom: 20px;
    }
  }

  &--select {
    select {
      margin-bottom: 20px;
      height: 40px;
      max-width: 300px;
      width: 100%;
    }
  }
}
