.contacts {
  @include vp--500 {
    margin: 20px;
    padding-top: 20px;
  }

  &__list {
    margin-bottom: 80px;

    @include vp--1023 {
      margin-bottom: 40px;
    }
  }

  &__item-wd {
    @include vp--1023 {
      max-width: 200px;
    }
  }

  a {
    opacity: 1;
    transition: opacity $trans--600;

    &:hover,
    &:focus,
    &:active {
      opacity: 0.6;
    }
  }
}
