.sidebar {
  min-width: 300px;

  @include vp--1279 {
    min-width: 200px;
  }

  @include vp--1023 {
    min-width: 100%;
    padding: 0 24px;
  }

  @include vp--767 {
    padding: 0 16px;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;

    @include vp--1023 {
      display: flex;
      margin-left: -20px;
    }

    @include vp--767 {
      flex-direction: column;
      justify-content: center;
      margin-left: -10px;
      margin-bottom: 40px;
    }
  }

  &__item {
    margin-bottom: 20px;

    @include vp--1023 {
      margin-left: 20px;
    }

    @include vp--767 {
      margin-left: 10px;
      padding: 5px 0;

      text-align: center;
      border: 1px solid $color-grey;
      border-radius: 10px;
    }
  }

  &__link {
    text-transform: uppercase;

    opacity: 1;
    transition: opacity $trans--300;

    @include vp--1023 {
      text-transform: none;
    }

    @include vp--767 {
      display: inline-block;
      vertical-align: middle;
      width: 100%;

      font-size: 12px;
      line-height: 18px;
    }

    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }

  &__link--active {
    text-decoration: underline;
    pointer-events: none;

    @include vp--767 {
      text-decoration: none;
      opacity: 0.6;
    }
  }
}
